body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  height: 100vh;
  position: relative;
}

.info-wrapper {
  position: absolute;
  right: 10px;
  top: 10px;
  background: black;
  color: white;
  z-index: 1;
  padding: 5px;
}

.info-wrapper > p:not(.info-score) {
  margin: 0px;
  font-size: 14px;
}

.info-score {
  font-size: 16px;
  text-align: center;
  margin: 5px;
}
